/* About.css */

.about-us-container {
  display: flex;
  align-items: center;
}

.about-us-image {
  border-radius: 50%; /* Makes the image round */
  width: 15%; /* Adjust the width of the image as needed */
  height: 15%; /* Adjust the height of the image as needed */
  margin-right: 20px; /* Adds some space between image and text */
}

.about-us-text {
  flex: 1; /* Allows the text to take remaining space */
  color: aliceblue;
}

/* Additional styling for h2 and p tags */
.about-us-text h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.about-us-text p {
  font-size: 16px;
  line-height: 1.6;
  text-align: left;
}
